import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent }       from './web-page/login/login.component';
import { HomeComponent }        from './web-page/home/home.component';
import { GreenComponent }       from './web-page/green/green.component';
import { OurVendorsComponent }  from './web-page/our-vendors/our-vendors.component';
import { ContactComponent }     from './web-page/contact/contact.component';
import { AboutUsComponent }     from './web-page/about-us/about-us.component';
import { FarmTableComponent }   from './web-page/farm-table/farm-table.component';
import { SpecialtyComponent }   from './web-page/specialty/specialty.component';
import { FloralComponent }      from './web-page/floral/floral.component';
import { DailyDealsComponent }  from './web-page/daily-deals/daily-deals.component';
import { WeeklyNewsComponent }  from './web-page/weekly-news/weekly-news.component';
import { LocationsComponent }   from './web-page/locations/locations.component';
import { InfraestructureComponent } from './web-page/infraestructure/infraestructure.component';
import { FoodSafetyComponent }  from './web-page/food-safety/food-safety.component';
import { ProteinComponent }     from './web-page/protein/protein.component';
import { ProduceComponent }     from './web-page/produce/produce.component';
import { OrganicComponent }     from './web-page/organic/organic.component';
import { DairyEggsCheesComponent } from './web-page/dairy-eggs-chees/dairy-eggs-chees.component';
import { DryGroceriesAndSuppliesComponent } from './web-page/dry-groceries-and-supplies/dry-groceries-and-supplies.component';
import { VideosComponent } from './web-page/videos/videos.component';
import { ImagesComponent } from './web-page/images/images.component';
import { ClientsComponent } from './clients-views/clients/clients.component';
import { ClientsFormComponent } from './clients-views/clients-form/clients-form.component';
import { ProductsFormComponent } from './products-views/products-form/products-form.component';
import { ProductsListComponent } from './products-views/products-list/products-list.component';

const routes: Routes = [
  {
    path: 'home',
    component: HomeComponent
  },
  {
    path: 'login',
    component: LoginComponent
  },
  {
    path: 'gallery/images',
    component: ImagesComponent
  },
  {
    path: 'gallery/videos',
    component: VideosComponent
  },
  {
    path: 'sustainability/going-green',
    component: GreenComponent
  },
  {
    path: 'sustainability/farm-to-table',
    component: FarmTableComponent
  },
  {
    path: 'community/weekly-newsletter',
    component: WeeklyNewsComponent
  },
  {
    path: 'community/daily-deals',
    component: DailyDealsComponent
  },
  {
    path: 'community/our-vendors-and-partners',
    component: OurVendorsComponent
  },
  {
    path: 'contact-us',
    component: ContactComponent
  },
  {
    path: 'about-us',
    component: AboutUsComponent
  },
  {
    path: 'products/dry-groceries-supplies',
    component: DryGroceriesAndSuppliesComponent
  },
  {
    path: 'products/dairy-eggs-cheese',
    component: DairyEggsCheesComponent
  }, 
  {
    path: 'products/organic',
    component: OrganicComponent
  }, 
  {
    path: 'products/produce',
    component: ProduceComponent
  }, 
  {
    path: 'products/protein',
    component: ProteinComponent
  },
  {
    path: 'products/specialty',
    component: SpecialtyComponent
  },
  {
    path: 'products/floral',
    component: FloralComponent
  },
  {
    path: 'locations',
    component: LocationsComponent
  },
  {
    path: 'infraestructure',
    component: InfraestructureComponent
  },
  {
    path: 'food-safety',
    component: FoodSafetyComponent
  },
  {
    path: 'clients',
    component: ClientsComponent
  },
  {
    path: 'clients/:id',
    component: ClientsFormComponent
  },  
  {
    path: 'client-register',
    component: ClientsFormComponent
  },
  {
    path: 'products',
    component: ProductsListComponent
  },
  {
    path: 'products/:id',
    component: ProductsFormComponent
  },  
  {
    path: 'product-register',
    component: ProductsFormComponent
  },
  {
    path: '**',
    pathMatch: 'full',
    redirectTo: 'home'
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
