import { Component, OnInit, HostBinding } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

import { Client } from '../../models/client'; 
import { ClientsService } from '../../services/clients.service';

@Component({
  selector: 'app-clients-form',
  templateUrl: './clients-form.component.html',
  styleUrls: ['./clients-form.component.css']
})
export class ClientsFormComponent implements OnInit {

  client: Client = {}

  edit: boolean = false;
  constructor(private clientService: ClientsService, private router: Router, private activatedRoute: ActivatedRoute) { }


  ngOnInit() {
  const params = this.activatedRoute.snapshot.params;
 
    if (params.id) {
      this.clientService.getClient(params.id)
        .subscribe(
          res => {
           
            console.log(res)
            this.client = res;
            console.log(this.client)
            console.log('msj '+this.client.Apellido_Materno_Cliente);
            this.edit = true;
          },
          err => console.log(err)
        )
    }
  }

  saveNewClient() {
    delete this.client.Id_Cliente;
    this.clientService.createClient(this.client)
      .subscribe(
        res => {
          console.log('msj'+res);
          this.router.navigate(['/clients']);
        },
        err => console.error(err)
      )
  }

  updateClient() {
    // alert(this.client.correo_cliente);
    this.clientService.updateClient(this.client.Id_Cliente, this.client)
      .subscribe(
        res => { 
          console.log(res);
          this.router.navigate(['/clients']);
        },
        err => console.error(err)
      )
  }
}
